import React from 'react';

const Button = ({ title, onClick, isActive }) => {
  return (
    <div>
      <button
        style={{
          maxWidth: "140px",
          minWidth: "80px",
          height: "30px",
          marginRight: "5px",
          backgroundColor: isActive ? "#4C566A" : "#2E3440",
          color: "#ECEFF4",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
          transition: "background-color 0.3s",
        }}
        onClick={onClick}
      >
        {title}
      </button>
    </div>
  );
};

export default Button;